	module.exports = {"data":{"markdownRemark":{"html":"<h2>Clustering Algorithms</h2>\n<p>Clustering algorithms have the task of grouping a set of objects in such a way that objects in the same group (called a cluster) are more similar (in some sense or another) to each other than to those in other groups (clusters). It is a main task of exploratory data mining, and a common technique for statistical data analysis, used in many fields, including machine learning, pattern recognition, image analysis, information retrieval, bioinformatics, data compression, and computer graphics.</p>\n<h2>Types of Clustering Algorithms:</h2>\n<ol>\n<li>Connectivity-based clustering (hierarchical clustering)</li>\n<li>Centroid-based clustering (k-means clustering)</li>\n<li>Distribution-based clustering </li>\n<li>Density-based clustering </li>\n</ol>\n<h2>Examples</h2>\n<ol>\n<li>Alogmerative clustering</li>\n<li>k-means clustering</li>\n<li>k-mediods clustering</li>\n<li>Partition Clustering</li>\n</ol>\n<h4>Suggested Reading:</h4>\n<ul>\n<li><a href=\"https://en.wikipedia.org/wiki/Cluster_analysis\">Cluster Analysis</a></li>\n<li><a href=\"https://www.analyticsvidhya.com/blog/2016/11/an-introduction-to-clustering-and-different-methods-of-clustering/\">Introduction to Clustering and related algorithms</a></li>\n</ul>","fields":{"slug":"/machine-learning/clustering-algorithms/"},"frontmatter":{"title":"Clustering Algorithms"}}},"pathContext":{"slug":"/machine-learning/clustering-algorithms/"}}

